import React, { useEffect, useState } from "react";
import { getFlags } from "../functions/flagsapi";

const newFlag = {
  id: null,
  name: "",
  population: "",
  region: "",
  capital: "",
  image: ""
};

export default function Home() {
  // state
  const [flags, setFlags] = useState([]);

  useEffect(loadFlags, []);

  function loadFlags() {
    getFlags().then(({ data }) => setFlags(data));
  }

  function renderFlagCard(flag) {
    const { id, name, population, region, capital, image } = flag;
    return (
      <div className="card" key={id}>
        <img src={image} alt={`Flag of ${name}`} className="card-img-top" />
        <div className="card-body">
          <h4 className="card-title">{name}</h4>
          <p className="card-title">Population: {population}</p>
          <p className="card-title">Region: {region}</p>
          <p className="card-title">Capital: {capital}</p>
        </div>
      </div>
    );
  }

  return (
    <section id="flag-wrapper" className="container">
      {flags.map(renderFlagCard)}
    </section>
  );
}
